.header {
    overflow: auto;
    height: 125px;
    padding-top: 25px;
    padding-bottom: 10px;
    border-bottom: 3px solid #00AD99;
}

.header .title {
    margin-top: 0px;
    margin-bottom: 0px;
}

.header .MuiGrid-root.MuiGrid-item {
    padding-top: 0px;
}

.navigation {
    margin-top: 0px;
    width: 100%;
    margin-left: auto;
}

/* Custom styling for MUI tabs */
.MuiButtonBase-root.MuiTab-root {
    font-size: 15px;
    font-weight: 700;
    padding-left: 0px;
    padding-right: 0px;
    opacity: 1;
}

.MuiButtonBase-root.Mui-selected {
    color: #D3D3D3;
}

.MuiTabs-indicator {
    background-color: #D3D3D3 !important;
}

.about {
    padding-top: 40px;
    text-align: center;
    position: absolute;
    top: 125px;
    bottom: 55px;
    overflow: auto;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.about .image {
    border-radius: 100px;
    border: 3px solid #00AD99;
    height: 300px;
}

.about .divider {
    border-top: 3px solid #00AD99;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
}

.portfolio {
    position: absolute;
    top: 125px;
    bottom: 55px;
    overflow: auto;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.portfolio .title {
    text-align: center;
}

.project .card {
    border: 3px solid #00AD99;
    background-color: #000000;
    color: #D3D3D3;
}

.project .box {
    position: relative;
}

.project .details {
    position: absolute;
    bottom: 0%;
    left: 0px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.70);
    color: #D3D3D3;
    padding: 10px;
}

.contact {
    position: absolute;
    top: 125px;
    bottom: 55px;
    overflow: auto;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.contact .title {
    text-align: center;
}

.contact .form {
    border: 2px solid;
    border-radius: 4px;
    padding: 12px;
    background-color: rgba(0, 0, 0, 0.70);
}

/* Manually adjusting colors for MUI text box field */
.contact .MuiInputBase-root {
    color: #D3D3D3;
}

.contact .MuiFormLabel-root, .contact .MuiFormLabel-root.Mui-focused {
    color: #00AD99;
}

.contact .MuiOutlinedInput-root fieldset, .contact .MuiOutlinedInput-root:hover fieldset, .contact .MuiOutlinedInput-root.Mui-focused fieldset {
    border-color: #00AD99;
}

.resume {
    margin-top: 40px;
    text-align: center;
    position: absolute;
    top: 125px;
    bottom: 55px;
    overflow: auto;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);

}

.footer {
    position: absolute;
    overflow: hidden;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 55px;
}

.footer h3 {
    font-size: 30px;
    margin-top: 0px;
    margin-bottom: 0px;
}

.tool {
    margin-bottom: 20px;
}

.ToolIcon {
    font-size: 40px;
}

.footer a:link, .footer a:visited, .footer a:hover, .footer a:active {
    color: #D3D3D3;
}

.project a:link, .project a:visited, .project a:hover, .project a:active {
    color: #007b6d;
}